import { Select } from 'antd';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styles from './FilterBox.module.css';
import { getMovieTicketAsync, selectMovieTicket } from './module/filterBoxSlice';

function FilterBox(props) {
  const { movieList } = props;
  const dispatch = useDispatch();
  const movieTicket = useSelector(selectMovieTicket);
  const { Option } = Select;
  // const [movieOptionList, setmovieOptionList] = useState([]);

  const handleChange = (e) => {
    const movieID = e.target.value;
    console.log("handleChange ~ movieID", movieID);
  }

  const renderMovieList = movieList?.map(movie => {
    return (
      <Option key={movie.maPhim} value={movie.maPhim}>{movie.tenPhim}</Option>
    );
  });


  return (
    <div className={`${styles.Search} ${styles.Container_lg} ${styles.p_0}`}>
      <div className={styles.Content}>
        <div className={styles.Header}>
          <div className={styles.Row}>
            <div className={styles.Col_9}>
              <div className={styles.Category}>WELCOME TO MOVIE TICKET</div>
              <div className={styles.Title}>WHAT ARE YOU LOOKING FOR</div>
            </div>
            <div className={`${styles.Col_3} ${styles.Text_Right}`}>
              <button className={styles.Btn_Gradient}>PROCEED</button>
            </div>
          </div>
        </div>
        <div className={styles.Bottom}>
          <form className={styles.SearchForm}>
            <div className={styles.Row}>
              <div className={`${styles.Col_Lg_3} ${styles.Col_Sm_6} ${styles.Col_Md_6} `}>
                <span>
                  <img src="data:image/png;base64," alt="movie" />
                </span>
                <span className={styles.Type}>Movie</span>
                <Select defaultValue={'Movie'} className={styles.Select}>
                  <Option disabled selected>Choose Movie</Option>
                  {renderMovieList}
                </Select>
              </div>
              <div className={`${styles.Col_Lg_3} ${styles.Col_Sm_6} ${styles.Col_Md_6} ${styles.Mt_Sm_0}`}>
                <span>
                  <img src="data:image/png;base64," alt="theater" />
                </span>
                <span className={styles.Type}>Theater</span>
                <Select defaultValue={'Theater'} className={styles.Select}>
                  <Option disabled selected>Choose Theater</Option>
                </Select>
              </div>
              <div className={`${styles.Col_Lg_3} ${styles.Col_Sm_6} ${styles.Col_Md_6}`}>
                <span>
                  <img src="data:image/png;base64," alt="date" />
                </span>
                <span className={styles.Type}>Date</span>
                <Select defaultValue={'Date'} className={styles.Select}>
                  <Option disabled selected>Choose Date</Option>
                </Select>
              </div>
              <div className={`${styles.Col_Lg_3} ${styles.Col_Sm_6} ${styles.Col_Md_6}`}>
                <span>
                  <img src="data:image/png;base64," alt="hour" />
                </span>
                <span className={styles.Type}>Hour</span>
                <Select defaultValue={'Hour'} className={styles.Select}>
                  <Option disabled selected>Choose Hour</Option>
                </Select>
              </div>
            </div>
          </form>
        </div>
      </div>

    </div>
  )
}

export default FilterBox
